.singlePost{
    flex: 9;

}

.singlePostWrapper{
    padding: 20px;
    padding-right: 0;
}

.singlePostImg{
    width: 100%;
    height: 100%;
    border-radius: 8px;
    object-fit: cover;
}

.singlePostTitle{
    font-family:'Playfair Display', sans-serif ;
    font-size: 30px;
    margin: 12px;
    text-align: center;
}

.singlePostEdit{
    float: right;
    font-size: 16px;
}

.singlePostIcon{
    margin-left: 16px;
    cursor: pointer;
}

.singlePostIcon:first-child{
    color: teal;
}
.singlePostIcon:last-child{
    color: tomato;

}

.singlePostInfo{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    color: #b39656;
}
.singlePostDesc{
    color: #666;
    font-size: 18px ;
    line-height: 25px;
}

..singlePostDesc::first-letter{
    margin-left: 20px;
    font-size: 30px;
    font-weight: 600;
}